/* ------------/////////////////////midea queries///////////////////// */

@media (min-width: 992px) and (max-width: 1064px) {
  .for_navbar .nav-link {
    margin-right: 20px !important;
    font-size: 1.2rem;
    font-weight: 400;
    /* margin: 0px !important; */
  }

}

@media (min-width: 1063px) and (max-width: 1250.98px) {
   .for_navbar .nav-link {
     margin-right: 25px !important;
     font-size: 1.2rem;
     font-weight: 400;
     /* margin: 0px !important; */
   }
}

@media (min-width: 200px) and (max-width: 407px) {
    .footer-size {
      transform: translateY(165px) !important;
    }
}

@media (min-width: 440px) and (max-width: 461px) {
  .footer_content {
    right: 18%;
  }
}

@media (min-width: 400px) and (max-width: 439px) {
  .footer_content {
    right: 15%;
  }
}



@media (min-width: 350px) and (max-width: 377px) {
  .footer_content {
    right: 7%;
  }
}

@media (min-width: 334px) and (max-width: 349px) {

